* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body, html, .app {
  font-family: 'Helvetica Neue', sans-serif;
  background-color: #f9f9f9;
  color: #222;
  line-height: 1.6;
}

.hero {
  background-color: #fff;
  padding: 80px 20px;
  text-align: center;
  box-shadow: 0 1px 4px rgba(0,0,0,0.05);
}

.container {
  max-width: 800px;
  margin: auto;
}

.name {
  font-size: 3rem;
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.title {
  font-size: 1.25rem;
  color: #777;
  margin-bottom: 1.5rem;
}

.intro {
  font-size: 1rem;
  max-width: 600px;
  margin: 0 auto;
}

.content {
  padding: 60px 20px;
  background-color: #f4f4f4;
}

.about {
  max-width: 700px;
  margin: auto;
  text-align: center;
}

.footer {
  text-align: center;
  padding: 30px 20px;
  font-size: 0.9rem;
  color: #888;
}
